import React, { Component } from 'react';
import Particles from 'react-particles-js';
import Title from '../components/Title';
import Messages from '../components/Messages';
import Footer from '../components/Footer';
import Tilt from 'react-tilt';
import '../containers/App.css';


class App extends Component {
  constructor(){
    super()
    this.state = {
      content: null,      
    }
  }

  async componentDidMount(){
    const resp = await fetch('https://api.kanye.rest/')
    const data = await resp.json();
    this.setState({content: data.quote})         
  }

  render (){
    return(
      <div>
        <Particles className='particles' params={{
                    particles: {
                        number: {
                          value:100,
                          density: {
                            enable: true,
                            value_area: 800
                          }
                        }
                    }}} /> 
        <div className='pt6 '>
        <div className='flex items-center justify-center tc pa4'>
          <Tilt className="Tilt" 
                  options={{ max : 40 }} style={{ height: 250, width: 250 }} >
                  <div className="Tilt-inner bg-washed-green mw5 h-auto pa3 br4 grow "> 
                  <Title />            
                  <Messages message={this.state.content}/> 
                </div>             
          </Tilt>       
        </div>                  
        <Footer />     
   </div>
</div>
     
      
  )
  
  };
}

export default App;
